@import "https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap";
*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --primary--color: #fff;
  --accent--color: #86e543;
  --header--color: #01383a;
  --footer--color: #001414;
  --secondary-color: #8d9289;
}

a, p, ul, li, h1, h2, h3, button, address {
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style-type: none;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

body {
  color: var(--primary--color);
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.18;
}

.container {
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media screen and (min-width: 480px) and (max-width: 767.9px) {
  .container {
    width: 480px;
    margin: 0 auto;
    padding-left: 15px;
    padding-right: 15px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    margin: 0 auto;
    padding-left: 40px;
    padding-right: 40px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
    margin: 0 auto;
    padding-left: 40px;
    padding-right: 40px;
  }
}

.header__container {
  min-width: 320px;
  background: #01383a;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  padding: 24px 16px;
  display: flex;
  position: relative;
  box-shadow: 0 4px 10px rgba(97, 162, 58, .2), 0 4px 50px rgba(94, 236, 54, .5), 0 4px 4px rgba(17, 168, 26, .2);
}

@media screen and (min-width: 480px) {
  .header__container {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .header__container {
    width: 768px;
    padding: 40px;
  }
}

@media screen and (min-width: 1280px) {
  .header__container {
    width: 1280px;
    padding: 0 40px;
  }
}

.header__container a {
  color: #fff;
  text-decoration: none;
}

.header__logo {
  text-align: center;
  font-size: 24px;
  font-weight: 700;
  line-height: 1.17;
}

.navigation {
  align-items: center;
  display: flex;
}

@media screen and (max-width: 1279px) {
  .navigation {
    display: none;
  }
}

.nav-list {
  align-items: center;
  gap: 48px;
  display: flex;
}

.nav-list__link {
  padding: 50px 0;
  font-size: 16px;
  font-weight: 400;
  line-height: 100%;
  display: block;
}

.header-button {
  cursor: pointer;
  background-color: #86e543;
  border: none;
  border-radius: 16px;
  padding: 16px 20px;
  font-size: 14px;
  font-weight: 700;
  line-height: 100%;
}

@media screen and (max-width: 767px) {
