    display: none;
  }
}

.mobile-menu-btn__icon {
  width: 54px;
  height: 36px;
}

.menu-container {
  width: 100vw;
  height: 100vh;
  z-index: 999;
  background-color: #fff;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  transform: translateX(110%);
}

@media screen and (min-width: 1280px) {
  .menu-container {
    display: none;
  }
}

.menu-container.is-open {
  transform: translateX(0);
}

.mobile-menu-btn-cls {
  background-color: rgba(0, 0, 0, 0);
  border-width: 0;
}

.mobile-menu-btn-cls__icon {
  width: 36px;
  height: 36px;
}

.menu-wrapper {
  min-width: 320px;
  height: 100%;
  background-color: #000;
  background-image: url("Background_MENU_mobile.e79e0a80.svg");
  background-repeat: no-repeat;
  background-size: cover;
  margin: 0 auto;
  padding-top: 168px;
  position: relative;
}

@media screen and (min-width: 480px) {
  .menu-wrapper {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .menu-wrapper {
    width: 768px;
    background-image: url("Background_MENU_tablet.e66b2caf.svg");
    padding-top: 256px;
  }
}

.menu-wrapper .header__container {
  position: absolute;
  top: 0;
  left: 0;
}

.mob-menu-list {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  font-size: 24px;
  font-weight: 700;
  line-height: 1.17;
}

@media screen and (min-width: 768px) {
