    background-image: url("back_mobile.f93f7fef.jpg");
  }
}

.section__title {
  text-align: center;
  color: var(--primary--color);
  margin-bottom: 35px;
  font-size: 24px;
  font-weight: 600;
  line-height: 1.16;
}

.list {
