  font-size: 20px;
  font-weight: 600;
  line-height: 100%;
}

.sale__button {
  width: 153px;
  height: 51px;
  text-align: center;
  color: #01383a;
  cursor: pointer;
  background: #fff;
  border-radius: 16px;
  margin: 0 auto;
  padding-top: 16px;
  padding-bottom: 16px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.18;
  text-decoration: none;
  display: block;
}

@media screen and (min-width: 768px) {
  .sale__button {
    width: 195px;
    height: 65px;
    font-size: 28px;
    line-height: 1.14;
  }
}

.link {
  text-decoration: none;
}

@media screen and (max-width: 767px) {
  .sale__hidden {
    display: none;
  }
}

.accessories {
  width: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
