    background: url("bg-mobile-2x.55d58993.jpg");
  }
}

@media screen and (min-width: 768px) {
  .accessories {
    background: url("bg-tablet.e74440e3.jpg");
  }
}

@media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2x) {
  .accessories {
    background: url("bg-tablet-2x.6ef0a3a2.jpg");
  }
}

@media screen and (min-width: 1200px) {
