}

.accessories__gallery {
  cursor: pointer;
  transition: box-shadow .25s cubic-bezier(.4, 0, .2, 1);
}

.accessories__top-wrap {
  position: relative;
  overflow: hidden;
}

.accessories__foto {
  border: 1px solid #86e543;
  border-radius: 12px;
  box-shadow: 0 4px 4px rgba(134, 229, 67, .2);
}

.accessories__top-text {
  color: #fff;
  height: 100%;
  width: 100%;
  background: linear-gradient(0deg, #01383a, #01383a);
