  top: 0;
  left: 0;
  transform: translateY(101%);
}

.accessories__top-text:hover, .accessories__top-text:focus {
  border: 1px solid #86e543;
  box-shadow: 0 4px 4px rgba(134, 229, 67, .2);
}

.accessories__gallery:hover .accessories__top-text, .accessories__gallery:focus .accessories__top-text {
  transform: translateY(0);
}

.accessories__info {
  padding: 8px 18px 28px;
}

.accessories__thumb {
  justify-content: space-between;
  align-items: baseline;
  display: flex;
}

.accessories__name {
  color: #86e543;
  flex: none;
  order: 0;
  margin-bottom: 8px;
  font-family: Roboto;
  font-size: 22px;
  font-style: normal;
  font-weight: 600;
  line-height: 1.18;
}

.accessories__price {
  color: #8d9289;
